import { useQuery } from '@tanstack/react-query'
import axios from 'axios'
import { FC, useState } from 'react'
import { FullArenaData } from './CharacterSearch'
import { z } from 'zod'
import humanizeDuration from "humanize-duration"
import { formatDistance } from "date-fns"

interface SearchedCharsProps {

}

const arenaDataSchema = z.object({
    character: z.string(),
    realm: z.string(),
    locale: z.string().refine(val => val === "eu" || val === "us"),
    twos: z.number(),
    threes: z.number(),
    rbgs: z.number(),
    shuffle: z.number(),
    shuffleRank: z.number(),
    time: z.number(),
    charClass: z.string(),
    charSpec: z.string()
})

type ArenaDataType = z.infer<typeof arenaDataSchema>

const arrayOfArenaDataSchema = z.array(arenaDataSchema)

const getAllChars = async (): Promise<ArenaDataType[]> => {
    try {
        const res = await axios.get("http://localhost:3000/arenaData")
        const parsed = arrayOfArenaDataSchema.safeParse(res.data)
        if (parsed.success) return parsed.data
        return []
    } catch (error) {
        console.error(error)
        return []
    }
}

const SearchedChars: FC<SearchedCharsProps> = ({ }) => {
    const [characters, setCharacters] = useState<ArenaDataType[]>([])

    const query = useQuery<ArenaDataType[]>(["searchedChars"],
        {
            queryFn: async () => {
                const data = await getAllChars()
                if (data) setCharacters(data)
                return data
            }
        })

    const time = new Date().getTime()

    const getHighestRating = (character: ArenaDataType) => {
        const highest = Math.max(character.rbgs, character.shuffle, character.threes, character.twos)
        const ladder = Object.keys(character).find(entry => {
            return character[entry as keyof ArenaDataType] === highest
        })
        return `${ladder}: ${highest}`
    }

    const capitalizeString = (str: string): string => {
        return str.charAt(0).toUpperCase() + str.slice(1).replaceAll("-", " ")
    }

    return <table className="w-full">
        <tr className="text-white p-3 rounded-md text-lg font-semibold font-sans">
            <th className="text-start">Name</th>
            <th className="text-start">Highest rating</th>
            <th className="text-start">Last time searched</th>
        </tr>
        {characters.map((char, i) => (
            <tr key={i} className="text-white  bg-blue-900 p-3 rounded-md text-lg font-normal font-sans">
                <td>{capitalizeString(char.character)} - {capitalizeString(char.realm)}</td>
                <td>{getHighestRating(char)}</td>
                <td>{formatDistance(time, char.time, { includeSeconds: true })} ago</td>
            </tr>
        ))}
    </table>
}

export default SearchedChars