import axios from 'axios'
import { FC, useEffect, useState } from 'react'
import { useLoaderData } from 'react-router-dom'
import { FieldValues, useForm } from "react-hook-form"
import { zodResolver } from "@hookform/resolvers/zod"
import { z } from 'zod'

interface RootProps {

}

const inputStyles = "rounded-xl py-1.5 px-2"

const signUpSchema = z.object({
  email: z.string().email({message: "Incorrectly fed email"}),
  password: z.string().min(10, "Password must be at least 10 characters"),
  confirmPassword: z.string()
}).refine((data) => data.confirmPassword === data.password, {
  message: "Passwords don't match",
  path: ["confirmPassword"]
})

type SignUpSchema = z.infer<typeof signUpSchema>

const Root: FC<RootProps> = ({ }) => {

  
useEffect(() => {
  axios.get("http://localhost:1337/healthcheck").then(res => console.log(res))
}, [])
  const loaderData = useLoaderData()
  console.log(loaderData)

  const { register, handleSubmit, formState: { isSubmitting, errors }, reset, setError } = useForm<SignUpSchema>({ resolver: zodResolver(signUpSchema) })

  /*  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
     const formData = new FormData(e.currentTarget)
     const postObject: any = {}
 
     formData.forEach((val, key) => postObject[key] = val)
     console.log(postObject)
     e.preventDefault()
     setSubmitting(true)
     axios.post("http://localhost:3000/test", postObject)
     await new Promise(res => setTimeout(res, 1000))
     setSubmitting(false)
   } */

  const onSubmit = (data: SignUpSchema) => {
    console.log(data)
    reset()
  }

  const validateEmail = (email: string) => {
    return String(email)
      .toLowerCase()
      .match(
        /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|.(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
      );
  };

  return <div className="w-[50%] flex justify-center mt-5 ">
    <form className="flex flex-col w-[25%] min-w-[200px] gap-3" onSubmit={handleSubmit(e => onSubmit(e))}>
      <input type="text" {...register("email")} className={inputStyles} />
      {errors.email ? <p className="text-red-500">{`${errors.email.message}`}</p> : ""}
      <input type="text" {...register("password")} className={inputStyles} />
      {errors.password ? <p className="text-red-500">{`${errors.password.message}`}</p> : ""}
      <input type="text" {...register("confirmPassword")} className={inputStyles} />
      {errors.confirmPassword ? <p className="text-red-500">{`${errors.confirmPassword.message}`}</p> : ""}
      <button type="submit" className={`p-3 m-5 rounded-xl ${isSubmitting ? "bg-blue-800" : "bg-blue-500"}`} disabled={isSubmitting}>Submit</button>
    </form>
  </div>
}

export default Root