import { FC, useEffect, useState } from 'react'
import axios, { AxiosError } from 'axios'
import { useSearchParams } from 'react-router-dom'
import { useForm } from 'react-hook-form'
import { z } from 'zod'
import { zodResolver } from '@hookform/resolvers/zod'

type ArenaData = {
    twos: number | null
    threes: number | null
    rbgs: number | null
    shuffle: number | null
    shuffleRank: number | null
}

export type FullArenaData = {
    character: string
    realm: string
    locale: string
    charSpec: string
    charClass: string
} & ArenaData

const boxNames = {
    twos: "2v2",
    threes: "3v3",
    rbgs: "RBG",
    shuffle: "Shuffle",
    shuffleRank: "Shuffle rank"
}

const specRanking = async (classSpec: string, character: string): Promise<number> => {
    const res = await axios.get(`https://eu.api.blizzard.com/data/wow/pvp-season/35/pvp-leaderboard/shuffle-${classSpec}?namespace=dynamic-eu&access_token=${process.env.REACT_APP_OAUTH_TOKEN}`)
    const data: any[] = res.data.entries
    const characterRank = data.filter((entry) => entry.character.name.toLowerCase() === character)
    return characterRank.length === 0 ? 999999 : characterRank[0].rank
}

const apiAddress = (character: string, realm: string, locale: string, category: string) => (`https://${locale}.api.blizzard.com/profile/wow/character/${realm}/${character.toLowerCase()}/pvp-bracket/${category}?namespace=profile-${locale}&locale=en_GB&access_token=${process.env.REACT_APP_OAUTH_TOKEN}`)

const getRatingFromAPI = async (character: string, realm: string, locale: string, category: string): Promise<number> => {
    try {
        const res = await axios.get(apiAddress(character, realm, locale, category))
        return res.data.rating
    } catch (error) {
        return 0
    }
}

const ratingSearchSchema = z.object({
    character: z.string({ required_error: "Need to provide user" }).min(1, "At least 2 characters"),
    realm: z.string({ required_error: "Provide a realm" }),
    locale: z.string({ required_error: "Provide locale" }).refine((val) => val === "us" || val === "eu", { message: "Locale must be either eu or us!" })
})


type ratingSearchSchemaType = z.infer<typeof ratingSearchSchema>

const getArenaData = async (character: string, realm: string, locale: string): Promise<FullArenaData> => {
    realm = realm.replaceAll(" ", "-")
    const charInfo = await axios.get(`https://${locale}.api.blizzard.com/profile/wow/character/${realm}/${character}?namespace=profile-${locale}&locale=en_US&access_token=${process.env.REACT_APP_OAUTH_TOKEN}`)
    const charClass = charInfo.data.character_class.name.toLowerCase()
    const charSpec = charInfo.data.active_spec.name.toLowerCase()
    const classSpec = `${charClass}-${charSpec}`
    const shuffle = await getRatingFromAPI(character, realm, locale, `shuffle-${classSpec}`)
    const twos = await getRatingFromAPI(character, realm, locale, `2v2`)
    const threes = await getRatingFromAPI(character, realm, locale, `3v3`)
    const rbgs = await getRatingFromAPI(character, realm, locale, `rbg`)
    const shuffleRank = await specRanking(classSpec, character)
    return { character, realm, locale, twos, threes, rbgs, shuffle, shuffleRank, charClass, charSpec }
}

const CharacterSearch: FC = ({ }) => {
    const [ratings, setRatings] = useState<ArenaData>({ twos: null, threes: null, rbgs: null, shuffle: null, shuffleRank: null })
    const { register, handleSubmit, formState: { isSubmitting, errors }, reset, setError } = useForm<ratingSearchSchemaType>({ resolver: zodResolver(ratingSearchSchema) })

    const onSubmit = async (data: ratingSearchSchemaType) => {
        try {
            const playerArenaData = await getArenaData(data.character.toLowerCase(), data.realm.toLowerCase(), data.locale)
            console.log(playerArenaData)
            await axios.post("http://localhost:3000/arenaData", playerArenaData)
            setRatings(prev => ({ twos: playerArenaData.twos, threes: playerArenaData.threes, rbgs: playerArenaData.rbgs, shuffle: playerArenaData.shuffle, shuffleRank: playerArenaData.shuffleRank }))
        } catch (error: any) {
            if (error.response.data.code === 404) setError('root', { message: "That character does not exist" })
            console.error(error.response.data.code)
        }
    }

    return <div className="flex flex-col justify-end items-center gap-10">
        <form className="flex flex-col gap-4 max-w-[50%]" onSubmit={handleSubmit(onSubmit)} >
            {isSubmitting ? <p>Submitting..</p> :
                <>
                    <select {...register("locale")}>
                        <option value="eu">EU</option>
                        <option value="us">US</option>
                    </select>
                    <input type="text" className="p-1" {...register("character")} />
                    {errors.character ? <p className="text-red-500">{`${errors.character.message}`}</p> : ""}
                    <input type="text" className="p-1" {...register("realm")} />
                    {errors.realm ? <p className="text-red-500">{`${errors.realm.message}`}</p> : ""}
                    <button type="submit" className="p-2 bg-indigo-800 text-white">Search arena data</button>
                    {errors.root ? <p className="text-red-500">{`${errors.root.message}`}</p> : ""}
                </>
            }
        </form>
        <div className="flex gap-5">
            {Object.keys(ratings).map((rating, i) => (
                <div className="w-[100px] h-[100px] bg-indigo-500 flex flex-col justify-around items-center" key={i}>
                    <h1 className="text-2xl font-semibold text-indigo-900 text-center leading-6">{boxNames[rating as keyof ArenaData]}</h1>
                    <h1 className="text-xl font-semibold text-indigo-200">{ratings[rating as keyof ArenaData]}</h1>
                </div>
            ))}
        </div>
    </div>
}


export default CharacterSearch