import { FC, useEffect, useState } from 'react'
import axios, { AxiosError } from 'axios'
import { useSearchParams } from 'react-router-dom'
import { useForm } from 'react-hook-form'
import { z } from 'zod'
import { zodResolver } from '@hookform/resolvers/zod'
import CharacterSearch from '../components/characters/CharacterSearch'
import SearchedChars from '../components/characters/SearchedChars'

type ArenaData = {
    twos: number | null
    threes: number | null
    rbgs: number | null
    shuffle: number | null
    shuffleRank: number | null
}

type FullArenaData = {
    character: string
    realm: string
    locale: string
} & ArenaData

const boxNames = {
    twos: "2v2",
    threes: "3v3",
    rbgs: "RBG",
    shuffle: "Shuffle",
    shuffleRank: "Shuffle rank"
}

const specRanking = async (classSpec: string, character: string): Promise<number> => {
    const res = await axios.get(`https://eu.api.blizzard.com/data/wow/pvp-season/35/pvp-leaderboard/shuffle-${classSpec}?namespace=dynamic-eu&access_token=${process.env.REACT_APP_OAUTH_TOKEN}`)
    const data: any[] = res.data.entries
    const characterRank = data.filter((entry) => entry.character.name.toLowerCase() === character)
    return characterRank.length === 0 ? 999999 : characterRank[0].rank
}

const apiAddress = (character: string, realm: string, locale: string, category: string) => (`https://${locale}.api.blizzard.com/profile/wow/character/${realm}/${character.toLowerCase()}/pvp-bracket/${category}?namespace=profile-${locale}&locale=en_GB&access_token=${process.env.REACT_APP_OAUTH_TOKEN}`)

const getRatingFromAPI = async (character: string, realm: string, locale: string, category: string): Promise<number> => {
    try {
        const res = await axios.get(apiAddress(character, realm, locale, category))
        return res.data.rating
    } catch (error) {
        return 0
    }
}

const getArenaData = async (character: string, realm: string, locale: string): Promise<FullArenaData> => {
    realm = realm.replaceAll(" ", "-")
    const charInfo = await axios.get(`https://${locale}.api.blizzard.com/profile/wow/character/${realm}/${character}?namespace=profile-${locale}&locale=en_US&access_token=${process.env.REACT_APP_OAUTH_TOKEN}`)
    const classSpec = `${charInfo.data.character_class.name.toLowerCase()}-${charInfo.data.active_spec.name.toLowerCase()}`
    const shuffle = await getRatingFromAPI(character, realm, locale, `shuffle-${classSpec}`)
    const twos = await getRatingFromAPI(character, realm, locale, `2v2`)
    const threes = await getRatingFromAPI(character, realm, locale, `3v3`)
    const rbgs = await getRatingFromAPI(character, realm, locale, `rbg`)
    const shuffleRank = await specRanking(classSpec, character)

    return { character, realm, locale, twos, threes, rbgs, shuffle, shuffleRank }
}

const ratingSearchSchema = z.object({
    character: z.string({ required_error: "Need to provide user" }).min(1, "At least 2 characters"),
    realm: z.string({ required_error: "Provide a realm" }),
    locale: z.string({ required_error: "Provide locale" }).refine((val) => val === "us" || val === "eu", { message: "Locale must be either eu or us!" })
})

type ratingSearchSchemaType = z.infer<typeof ratingSearchSchema>

const Character: FC = ({ }) => {



    return <div className="flex flex-col justify-around items-center gap-5 container  h-[80%]">
        <CharacterSearch />
        <SearchedChars />
    </div>
}

export default Character